* {
  box-sizing: border-box;
}

body {
  font-family: sans-serif;
  margin: auto;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}


.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 128px;
  font-weight: bold;
  line-height: 258px;
  height: 258px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 258px;
}

.board-row:after {
  clear: both;
  content: '';
  display: table;
}

.status {
  margin-bottom: 10px;
}
.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}

.winner {
  background-color: yellow; /* Highlight the winning squares with a yellow background */
}